        </div>
      </div></li>
    </ul>
  </div>

<div class="col-md-12 col-sm-12 p-0 float-left mainChart">
    <div class="row-12">
        <!-- apex column charts for productivity bar -->
        <div class="border rounded p-2 my-3">
        <VueApexCharts v-if="isShowProductivityBar" width="100%" height="350px" type="bar" :options="options"
          :series="series"></VueApexCharts>
      </div>
    </div>
  </div>
  <div class="col-md-12 col-sm-12 p-0 float-left">
    <div class="row d-flex justify-content-between mx-0 timetrackdb_pb">
      <!-- ProductiveApps -->
      <TimerLoader :is-visible="ProductiveAppsLoader"></TimerLoader>
      <div class="col-3 border productive-box p-0 mb-4 scroll " style="max-height: 444px; overflow-y: auto; display: block;">
        <h6 class="border-bottom p-2 mb-0">{{$t('ProductiveApps')}}<span class="float-right">{{ ProductiveTotalTime }}</span></h6>
        <div v-if="ProductiveApps.length === 0" class="text-center mt-4 error-message">
