        <h6 v-if="isShowViewAllProductive == false" class="border-top p-2 text-center" style="cursor: pointer"
          v-on:click="collapse('productive')">
          Collapse <em class="fa fa-angle-up" aria-hidden="true"></em>
        </h6> -->
      </div>
      <!-- Unproductive Apps  -->
      <TimerLoader :is-visible="UnProductiveAppsLoader"></TimerLoader>
      <div class="col-3 border productive-box p-0 mb-4 scroll " style="max-height: 444px; overflow-y: auto; display: block;">
        <h6 class="border-bottom p-2 mb-0">{{$t('UnproductiveApps')}} <span class="float-right">{{ UnProductiveTotalTime }}</span></h6>
        <div v-if="UnProductiveApps.length === 0" class="text-center mt-4 error-message">
          <p class="error-text">No records found.</p>
        </div>
        <div class="row mx-3 py-3 dotted-border d-flex align-items-center" v-for="(app, index) in UnProductiveApps" :key="index">
          <div :class="app.icon_class ? 'appicon ' + app.icon_class : 'appicon default'"></div>
          <div class="col-4" >
            <div class="" :title="app.application_display_name">
            <span class="font-15">{{ TruncateStr(app.application_display_name, 10) }}</span> <br />
          </div>
            <small :title="app.category_name">{{ TruncateStr(app.category_name, 12) }}</small>
          </div>
          <div class="col-4">
            <small class="text-center" :style="{ color: app.progress_bar_options.progress.color }">{{
              app.percentage_duration }}%</small>
            <br />
            <ProgressBar :options="app.progress_bar_options" :value="app.percentage_duration" />
          </div>
          <div class="col-3 text-right">
            <small>{{ app.duration_time }}</small>
          </div>
        </div>
        <!-- <h6 v-if="UnProductiveApps.length > 5 && isShowViewAllUnProductive" class="border-top p-2 text-center"
          style="cursor: pointer" v-on:click="ViewAllApps('unproductive')">
           {{$t('ViewAll')}}
        </h6>
        <h6 v-if="isShowViewAllUnProductive == false" class="border-top p-2 text-center" style="cursor: pointer"
          v-on:click="collapse('unproductive')">
          Collapse <em class="fa fa-angle-up" aria-hidden="true"></em>
        </h6> -->
      </div>
      <!-- Neutral Apps -->
      <TimerLoader :is-visible="NeutralAppsloader"></TimerLoader>
      <div class="col-3 border productive-box p-0 mb-4 scroll " style="max-height: 444px; overflow-y: auto; display: block;">
        <h6 class="border-bottom p-2 mb-0">
           {{$t('NeutralApps')}}<span class="float-right">{{ NeutralTotalTime }}</span>
        </h6>
        <div v-if="NeutralApps.length === 0" class="text-center mt-4 error-message">
          <p class="error-text">No records found.</p>
        </div>
        <div class="row mx-3 py-3 dotted-border d-flex align-items-center" v-for="(app, index) in NeutralApps" :key="index">
          <div :class="app.icon_class ? 'appicon ' + app.icon_class : 'appicon default'"></div>
          <div class="col-4">
            <div class="" :title="app.application_display_name">
            <span class="font-15">{{ TruncateStr(app.application_display_name, 10) }}</span> <br />
            </div>
            <small :title="app.category_name">{{ TruncateStr(app.category_name, 12) }}</small>
          </div>
          <div class="col-4">
            <small class="text-center" :style="{ color: app.progress_bar_options.progress.color }">{{
              app.percentage_duration }}%</small>
            <br />
          <div>
            <ProgressBar :options="app.progress_bar_options" :value="app.percentage_duration" />
          </div>
          </div>
          <div class="col-3 text-right">
            <small>{{ app.duration_time }}</small>
          </div>
        </div>
        <!-- <h6 v-if="NeutralApps.length > 5 && isShowViewAllNeutral" class="border-top p-2 text-center"
          style="cursor: pointer" v-on:click="ViewAllApps('neutral')">
           {{$t('ViewAll')}} <em class="fa fa-angle-down" aria-hidden="true"></em>
        </h6>
        <h6 v-if="isShowViewAllNeutral==false" class="border-top p-2 text-center"
          style="cursor: pointer" v-on:click="collapse('neutral')">
           {{$t('Collapse')}} <em class="fa fa-angle-up" aria-hidden="true"></em>
        </h6> -->
      </div>
      <div class="col-3 border productive-box p-0 mb-4">
        <TimerLoader :is-visible="mostusedAppsDataloader"></TimerLoader>
        <h6 class="border-bottom p-2">{{$t('MostUsedApps')}}</h6>
        <div v-if="mostusedAppsData.length === 0" class="text-center mt-4 error-message">
          <p class="error-text">No records found.</p>
        </div>
        <div id="chart">
          <VueApexCharts v-if="mostuserappdata" type="bar" height="300" :options="chartOptions" :series="series1">
          </VueApexCharts>
        </div>
      </div>
    </div></div>

    
    <!-- Catagories -->
    
    <div class="col-md-12 col-sm-12 p-0 float-left">
      <div class="col-12 border px-0">
        <h6 class="mt-2 pl-2">{{$t('Categories')}}</h6>
        <div v-if="AppDurationByCategories.length === 0" class="text-center mt-4 error-message">
          <p class="error-text">{{ $t('No_Record_Found') }}</p>
        </div>
        <span v-for="(item, index) in AppDurationByCategories" :key="index">
          <small class="colorboxsquare" v-bind:style="{ background: item.color_code }"></small>
          <small class="mr-3">{{ item.category_name }}</small>
        </span>
        <div><br></div>
        <div class="progress">
          <div class="progress-bar" v-for="item in AppDurationByCategories" style="margin-right: 3px;"  :style="{ width: item.percentage_duration + '%' }">            
            <div class="center h6" style="justify-content: center;margin-bottom: 0;" :title="item.duration_time"
              v-bind:style="{ background: item.color_code }" :key="index" role="progressbar"
              :aria-valuenow="item.percentage_duration" aria-valuemin="0" aria-valuemax="100">
              {{ item.duration_time }}
             
            </div>
          </div>
        </div>
           <br>
      </div>
    </div>
   
  </section>
</template>
<script>
var TimeRange = [];
var dateType = "DD";
var GetTimerDashboard =[];
import VueApexCharts from "vue-apexcharts";
import DataService from "../../services/DataService";
import ProgressBar from "vuejs-progress-bar";
import TimerLoader from '../../components/Common/TimerProductiveLoader.vue'

export default {
  components: { VueApexCharts, ProgressBar,TimerLoader },
  data() {
    return {
      User: "1",
      ddlUser: [
        {
          id: "1",
          Name: "My View",
        },
        {
          id: "2",
          Name: "Department View",
        },
        {
          id: "3",
          Name: "User View",
        },
      ],
      //Start Productive bar line bar graph
      isShowProductivityBar: false,
      mostuserappdata: false,
      options: {
        colors: ["#42c683", "#fcae18", "#c0c0c0", "#ededed"],
        chart: {
          type: 'bar',
          height: 400,
          stacked: true,
          toolbar: {
            show: false
          },
          zoom: {
            enabled: true
          }
        },
        responsive: [{
          breakpoint: 480,
          options: {
            legend: {
              position: 'bottom',
              offsetX: -10,
              offsetY: 0
            }
          }
        }],
        plotOptions: {
          bar: {
            horizontal: false,
            columnWidth: "100%",
          }
             }, 
              dataLabels: {
              enabled: false
            },     
        stroke: {
          width: 1,
          colors: ["#fff"]
        },
        title: {
          text: "Productivity Graph",

          align: 'left',
    margin: 0,
    offsetX: 0,
    offsetY: 30,
   // floating: false,
    // style: {
    //   fontSize:  '14px',
    //   fontWeight:  'bold',
    //   fontFamily:  undefined,
    //   color:  '#263238'
    // },
        },
        xaxis: {
          categories: [],
          labels: {
            formatter: function (value) {
              if (dateType == "DD") {
                if(value.includes(":00")>0)
                  {
                    value = value.replace(':00', '');
                    if (value[0] == '0') {
                      value = value.substring(1);
                    }
                  }
                  else
                  {
                  value="";
                  }
                return value ;
              } 
              else if (dateType == "WK") {

                if (value == 'Monday 00:00:00' || value == 'Tuesday 00:00:00'|| value == 'Wednesday 00:00:00'|| value == 'Thursday 00:00:00'|| value == 'Friday 00:00:00'|| value == 'Saturday 00:00:00'|| value == 'Sunday 00:00:00')
                {
                  value =value.replace(/ 00:00:00/,'');
                }
                else
                {
                  value="";
                }
                return value;
                }
              else {
                return value;
              }
            },
          },        
        },
          yaxis: {
          min: 0,
          max: 100,
          tickAmount: 4,
        },
        tooltip: {
          enabled: true,
          y: {
            formatter: function (value,x) {
              let item=x.dataPointIndex;
              var month=x.w.globals.labels[item];
             if( dateType == "DD") {
                // for (var item in GetTimerDashboard) {
                  if( GetTimerDashboard[item].ProductivePer === value || GetTimerDashboard[item].UnProductivePer
                  || GetTimerDashboard[item].NeutralPer === value || GetTimerDashboard[item].NoRecord === value ){
                    if(GetTimerDashboard[item].Total != 0){
                      if(GetTimerDashboard[item].NoRecord ==0 ){
                        return (
                        '<div class="arrow_box">' +
                        "<span>" +
                          value.toFixed(2) +
                        "%, Time : " +
                            month+
                        "</span>" +
                        "</div>"
                      );
                      }
                      else
                      {
                        return ''+ ', Time : '+x.w.config.xaxis.categories[item] ;
                      }
                  }
                  else{
                    return ''+ ', Time : '+x.w.config.xaxis.categories[item] ;
                  }
                 }
               //  }
              }
             if( dateType == "WK") {
              // Customize the tooltip content here
              //for (var item in GetTimerDashboard) {
                  if( GetTimerDashboard[item].ProductivePer === value || GetTimerDashboard[item].UnProductivePer
                  || GetTimerDashboard[item].NeutralPer === value || GetTimerDashboard[item].NoRecord === value ){
                    if(GetTimerDashboard[item].Total != 0){
                      if(GetTimerDashboard[item].NoRecord ==0 ){
                        return (
                          '<div class="arrow_box">' +
                          "<span>" +
                            value.toFixed(2) +
                          "%, Time : " +
                              month+
                          "</span>" +
                          "</div>"
                        );
                        }
                        else
                        {
                          return ''+ ', Time : '+x.w.config.xaxis.categories[item] ;
                        }
                  }
                  else
                        {
                          return ''+ ', Time : '+x.w.config.xaxis.categories[item] ;
                        }
                  }
                // }
              }
              if (dateType == "MM") {
                // Customize the tooltip content here
               // for (var item in GetTimerDashboard) {
                  if(GetTimerDashboard[item].NoRecord === value || GetTimerDashboard[item].ProductivePer === value || GetTimerDashboard[item].UnProductivePer
                  || GetTimerDashboard[item].NeutralPer === value){
                    if(GetTimerDashboard[item].Total != 0){
                      var strmsg='Arrival Time:' + GetTimerDashboard[item].temp[0].track_start_time
                        + '</br>Left Time:'  + GetTimerDashboard[item].temp[0].track_stop_time
                        + '</br>Idle Time:'  + GetTimerDashboard[item].temp[0].IdleTimestr
                      if(x.seriesIndex==0)  
                      {
                        strmsg = strmsg + '</br>Productivity(%):' + GetTimerDashboard[item].ProductivePer;
                         return strmsg;
                      }
                      if(x.seriesIndex==1)  
                         return strmsg + '</br>Unproductivity(%):' + GetTimerDashboard[item].UnProductivePer ; 
                      if(x.seriesIndex==2)  
                         return strmsg + '</br>Neutral(%):' + GetTimerDashboard[item].NeutralPer ;   
                    }
                  else{
                    return 'No Record' + ', Time : '+x.w.config.xaxis.categories[item] ;
                  }
                  }
                //}
              }
              
             
            },
            title: {
            formatter: function (seriesName) {
              if( dateType == "DD") {
                return seriesName
              }
              if( dateType == "WK") {
                return seriesName
              }
              if( dateType == "MM") {
                return ''
              }
            }
           }
          },
        },
        // yaxis: {
        //   min: 0,
        //   max: 100
        // },
        legend: { position: "top", horizontalAlign: "right", offsetX: 40 },
      },
      series: [
        { name: "Productive App", data: [] },
        { name: "Neutral App", data: [] },
        { name: "Unproductive App", data: [] },
        { name: "No Record", data: [] },
      ],
      //colors: ["#3c8028","#FFA500","#84857f" ],
      fill: { type: "solid" },
      xaxis: { type: "" },
      //End Productive bar line bar graph
      //Start horizontal chart for Most Used apps
      chartOptions: {
        //colors: ["#3c8028","#84857f","#FFA500" ],
        colors: ["#42c683", "#c0c0c0", "#fcae18"],
        chart: { 
          type: "bar",
         height: 450 ,
         width:250,
         toolbar: {
            show: false
          },},
                plotOptions: {
              bar: {
                horizontal: true,
                endingShape: "rounded",
                dataLabels: {
                  //position: 'top',
                },
              }
            },
        dataLabels: { enabled: false },
        dataLabels: {
              enabled: true,
              formatter: function (val) {
                return val + "%";
              },
              //offsetY: 5,
              style: {
                fontSize: '12px',
              }
            },
        xaxis: {
          min: 0,
          max:100,
          categories: [],
         labels: {
            formatter: function (val) {
                return val + "%";
            }
