  async created() {
    var vm = this;
    vm.userId = vm.$route.params.id;
    await vm.GetUserNameandShift();
    dateType = "DD";
    vm.fromDate = new Date();
    vm.fromDate = vm.ConvertDateFormatDate(vm.fromDate);
    vm.ToDate = vm.fromDate + " 23:59:59";
    vm.formatDuration();
    TimeRange = vm.getTimeRanges(60);
    var todayDate = new Date();
    var currentMonthDays = new Date(
      todayDate.getFullYear(),
      todayDate.getMonth() + 1,
      0
    ).getDate();
    var dateBefore7Days = new Date();
    dateBefore7Days.setDate(dateBefore7Days.getDate() - 6);
    var dateBeforeThirtyDays = new Date();
    dateBeforeThirtyDays.setDate(
      dateBeforeThirtyDays.getDate() - currentMonthDays
    );
    vm.monthEndDate = dateBeforeThirtyDays;
    vm.endDate = dateBefore7Days;
    vm.weekDate = {
      start: vm.startDate,
      end: vm.endDate,
    };
    vm.monthRange = {
      start: vm.monthStartDate,
      end: vm.monthEndDate,
    };
      // vm.GetArrivalTime();
    // await vm.GetTimerDashboard();
    // await vm.GetAllApps();
    // await vm.GetMostUsedApps();
    // await vm.GetUsedAppTimeDurationByCategories();
  },
  updated() {
    var vm = this;
    const newDate = vm.ConvertDateFormat(new Date());
    if (vm.dateType == "WK") {
      var dateBefore7Days = new Date();
      dateBefore7Days.setDate(dateBefore7Days.getDate() - 6);
      const startDate = vm.ConvertDateFormat(vm.startDate);
      const endDate = vm.ConvertDateFormat(vm.endDate);
      const dateBeforeSevenDays = vm.ConvertDateFormat(dateBefore7Days);
      if (!(startDate == newDate && endDate == dateBeforeSevenDays)) {
        const sevenDaysLater = new Date(vm.startDate);
        sevenDaysLater.setDate(vm.startDate.getDate() + 6);
        const diffTime = Math.abs(vm.endDate - vm.startDate);
        const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
        // if (vm.endDate > sevenDaysLater || diffDays < 6) {
        //   vm.weekDate = {
        //     start: vm.startDate,
        //     end: sevenDaysLater,
        //   };
        // }
      }
    } 
     else if (vm.dateType == "MM") {
        if (typeof vm.monthStartDate !== 'undefined' && vm.monthStartDate !== null) {
            var todayDate = new Date();
            const dateBefore30Days = new Date();
            var currentMonthDays = new Date(
                todayDate.getFullYear(),
                todayDate.getMonth() + 1,
                0
            ).getDate();
            dateBefore30Days.setDate(dateBefore30Days.getDate() - currentMonthDays);
            const dateBeforeThirtyDays = vm.ConvertDateFormatDate(dateBefore30Days);
            const monthStartDate = vm.ConvertDateFormatDate(vm.monthStartDate);
            const monthEndDate = vm.ConvertDateFormat(vm.monthEndDate);
            const thirtyDaysLater = new Date(vm.monthStartDate);
            thirtyDaysLater.setDate(vm.monthStartDate.getDate() + currentMonthDays);

            if (!(monthStartDate.getTime() === dateBeforeThirtyDays.getTime() && monthEndDate.getTime() === newDate.getTime())) {
                const diffTime = Math.abs(vm.monthEndDate - vm.monthStartDate);
                const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
                if (vm.monthStartDate > thirtyDaysLater || diffDays < 31) {
                    vm.monthRange = {
                        start: vm.monthStartDate,
                        end: thirtyDaysLater,
                    };
                }
            } else if (vm.updateMonthRange) {
                vm.monthRange = {
                    start: vm.monthStartDate,
                    end: thirtyDaysLater,
                };
                vm.updateMonthRange=false;
            }
        }
    }
  },

