      vueObj.ProductiveLoader = false;
      vueObj.chartOptions.xaxis.categories = [];
      vueObj.series1[0].data = [];
      vueObj.series1[1].data = [];
      vueObj.series1[2].data = [];
      var param = "";
      vueObj.mostuserappdata = false;
      vueObj.mostuserappdataLoading = true;
      if ((vueObj, dateType == "DD")) {
        param = `fromDate=${vueObj.fromDate}&toDate=${vueObj.ToDate}&type=${vueObj.dateType}&userIds=${vueObj.selecteduser == undefined ? '' : vueObj.selecteduser}&viewType=DepartmentView&IsShiftView=${vueObj.isShiftView}`;
      } else if (vueObj.dateType == "WK") {
        param = `fromDate=${vueObj.ConvertDateFormatDate(vueObj.startDate)}&toDate=${vueObj.ConvertDateFormat(vueObj.endDate)}&type=${vueObj.dateType}&userIds=${vueObj.selecteduser == undefined ? '' : vueObj.selecteduser}&viewType=DepartmentView&IsShiftView=${vueObj.isShiftView}`;
      } else {
        param = `fromDate=${vueObj.ConvertDateFormatDate(vueObj.monthStartDate)}&toDate=${vueObj.ConvertDateFormat(vueObj.monthEndDate)}&type=${vueObj.dateType}&userIds=${vueObj.selecteduser == undefined ? '' : vueObj.selecteduser}&viewType=DepartmentView&IsShiftView=${vueObj.isShiftView}`;
      }
      await DataService.GetMostUsedApps(param).then((response) => {
        vueObj.mostusedAppsData = response.data.data;
        if (vueObj.mostusedAppsData != null) {
          vueObj.mostusedAppsData.forEach((item, index) => {
            vueObj.chartOptions.xaxis.categories.push(
              item.application_display_name
            );
            if (item.productive_type == "Productive") {
              vueObj.series1[0].data.push({x:item.application_display_name,y:item.percentage_duration,fillColor:"#42c683"}); 
            }
            if (item.productive_type == "UnProductive")
            {
           vueObj.series1[0].data.push({x:item.application_display_name,y:item.percentage_duration,fillColor:"#c0c0c0"}); 
            }
            if  (item.productive_type == "Neutral") {
             vueObj.series1[0].data.push({x:item.application_display_name,y:item.percentage_duration,fillColor:"#fcae18"}); 
            }
            vueObj.mostuserappdata = true;
          });
          vueObj.mostuserappdataLoading = false;
        } else {
          vueObj.mostusedAppsData = [];
        }
      });
    },
    GetUsedAppTimeDurationByCategories: async function () {
      var vueObj = this;
      var param = "";
      vueObj.categoriesloader=true;
      param = `fromDate=${vueObj.fromDate}&toDate=${vueObj.ToDate}&userIds=${vueObj.selecteduser == undefined ? '' : vueObj.selecteduser}&type=${vueObj.dateType}&viewType=DepartmentView&IsShiftView=${vueObj.isShiftView}`;
      if ((vueObj, dateType == "DD")) {
      } else if (vueObj.dateType == "WK") {
        param = `fromDate=${vueObj.ConvertDateFormatDate(vueObj.startDate)}&toDate=${vueObj.ConvertDateFormat(vueObj.endDate)}&userIds=${vueObj.selecteduser == undefined ? '' : vueObj.selecteduser}&type=${vueObj.dateType}&viewType=DepartmentView&IsShiftView=${vueObj.isShiftView}`;
      } else {
        param = `fromDate=${vueObj.ConvertDateFormatDate(vueObj.monthStartDate)}&toDate=${vueObj.ConvertDateFormat(vueObj.monthEndDate)}&userIds=${vueObj.selecteduser == undefined ? '' : vueObj.selecteduser}&type=${vueObj.dateType}&viewType=DepartmentView&IsShiftView=${vueObj.isShiftView}`;
      }
      await DataService.GetUsedAppTimeDurationByCategories(param).then((response) => {
        vueObj.AppDurationByCategories = response.data.data;
        if (vueObj.AppDurationByCategories != null) {
          vueObj.AppDurationByCategories.forEach((item, index) => {
            var objProgressOption = {
              text: {
                color: "#FFFFFF",
                shadowEnable: true,
                shadowColor: "#000000",
                fontSize: 12,
                fontFamily: "Helvetica",
                dynamicPosition: false,
                hideText: true,
              },
              progress: {
                color: item.color_code,
                backgroundColor: "#F2F2F2",
                inverted: false,
                width: item.percentage_duration,
              },
              layout: {
                height: 10,
                width: item.percentage_duration,
                verticalTextAlign: 61,
                horizontalTextAlign: 43,
                zeroOffset: 0,
                strokeWidth: 30,
                progressPadding: 0,
                type: "line",
              },
            };
            vueObj.AppDurationByCategories[index].progress_bar_options = objProgressOption;

          });
          vueObj.TempProductiveApps = [];
          if (vueObj.ProductiveApps) {
            vueObj.ProductiveApps.forEach((item, index) => {
              if (index < 5) {
                vueObj.TempProductiveApps.push(item);
              }
            });
          }
          vueObj.categoriesloader=false;
        }
        else{
          vueObj.categoriesloader=false;
        }
      }
      );
    },
  },
}
</script>
