<template>
    
    <div v-if="isVisible" class="" id="divProgress" style="display: block;">
        <div class="lds-ring">
            <img  style="max-width: 50%; position: absolute; top: 18px; left: 19px; " class="blob" alt="">
            <div></div>
            <div></div>
            <div></div>
            <div></div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "TimerProductiveLoader",
        props: {
            isVisible: { type: Boolean, required: true },
            text: { type: String, required: false, default: "" },
        },
    }
